/* global Office console */

const generateEmailBody = (data) => {
  return `
    <div style="font-family: Arial, sans-serif; line-height: 1.6; max-width: 600px; margin: 0 auto;">
      <h2 style="color: #333; font-size: 24px; margin-bottom: 20px;">Service Level Details</h2>
      <div style="background-color: #f9f9f9; padding: 20px; border-radius: 5px; border: 1px solid #ddd; margin-bottom: 20px;">
        <p style="margin: 0;"><strong>Service Level 1:</strong> ${data.ServiceLevel1}</p>
        <p style="margin: 0;"><strong>Service Level 2:</strong> ${data.ServiceLevel2}</p>
      </div>
      <div style="padding: 20px; border-radius: 5px; border: 1px solid #ddd; margin-bottom: 20px;">
        <p style="margin: 0;"><strong>Issue Description:</strong></p>
        <p style="margin: 10px 0;">${data.IssueDescription}</p>
      </div>
    </div>
  `;
};


const createAndInsertEmail = async (data) => {
  try {
    const emailBody = generateEmailBody(data);
    const emailArray = ["mohammed.musthafa@ligabue.it"];
    const subject = `New Ticket: ${data.ServiceLevel1} - ${data.ServiceLevel2}`;

    // Set the content in the mail body
    Office.context.mailbox.item.body.setSelectedDataAsync(
      emailBody,
      { coercionType: Office.CoercionType.Html },
      function (result) {
        if (result.status === Office.AsyncResultStatus.Succeeded) {
          Office.context.mailbox.item.to.setAsync(emailArray);
          Office.context.mailbox.item.subject.setAsync(subject);
          console.log("Content set in the mail body successfully.");
        } else {
          console.error(result.error);
        }
      }
    );
  } catch (error) {
    console.log("Error: " + error);
  }
};

export default createAndInsertEmail;
