import React, { useState, useEffect } from "react";
import data from "./serviceLevelData";
import createAndInsertEmail from "../office-document";

export default function App() {
  const [serviceLevel1, setServiceLevel1] = useState("");
  const [serviceLevel2, setServiceLevel2] = useState("");
  const [subcategories, setSubcategories] = useState([]);
  const [textAreaContent, setTextAreaContent] = useState("");
  const [isFormValid, setIsFormValid] = useState(false);

  useEffect(() => {
    if (serviceLevel1) {
      const selectedCategory = data.ServiceLevel1.find((item) => item.category === serviceLevel1);
      setSubcategories(selectedCategory ? selectedCategory.subcategories : []);
      setServiceLevel2("");
    } else {
      setSubcategories([]);
    }
  }, [serviceLevel1]);

  useEffect(() => {
    setIsFormValid(serviceLevel1 !== "" && serviceLevel2 !== "" && textAreaContent !== "");
  }, [serviceLevel1, serviceLevel2, textAreaContent]);

  const handleSubmit = (e) => {
    e.preventDefault();

    const selectedData = {
      ServiceLevel1: serviceLevel1,
      ServiceLevel2: serviceLevel2,
      IssueDescription: textAreaContent,
    };

    console.log(selectedData);
    createAndInsertEmail(selectedData);
  };

  return (
    <div className="min-h-screen flex items-center justify-center bg-gray-100 py-6 px-4 sm:px-6 lg:px-8">
      <div className="max-w-md w-full bg-white p-8 rounded-xl shadow-md space-y-6">
        <h1 className="text-3xl font-bold text-center text-gray-900">Raise Ticket</h1>
        <form onSubmit={handleSubmit} className="space-y-4">
          <div className="space-y-2">
            <label className="block text-sm font-medium text-gray-700">Service Level 1</label>
            <select
              value={serviceLevel1}
              onChange={(e) => setServiceLevel1(e.target.value)}
              className="block w-full px-3 py-2 mt-1 border border-gray-300 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
            >
              <option value="">Select Service Level 1</option>
              {data.ServiceLevel1.map((item, index) => (
                <option key={index} value={item.category}>
                  {item.category}
                </option>
              ))}
            </select>
          </div>
          <div className="space-y-2">
            <label className="block text-sm font-medium text-gray-700">Service Level 2</label>
            <select
              value={serviceLevel2}
              onChange={(e) => setServiceLevel2(e.target.value)}
              className="block w-full px-3 py-2 mt-1 border border-gray-300 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
            >
              <option value="">Select Service Level 2</option>
              {subcategories.map((subcategory, index) => (
                <option key={index} value={subcategory}>
                  {subcategory}
                </option>
              ))}
            </select>
          </div>
          <div className="space-y-2">
            <label className="block text-sm font-medium text-gray-700">Issue Description</label>
            <textarea
              value={textAreaContent}
              onChange={(e) => setTextAreaContent(e.target.value)}
              rows="4"
              className="block w-full px-3 py-2 mt-1 border border-gray-300 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
            />
          </div>
          <button
            type="submit"
            className="w-full py-2 px-4 border border-transparent text-sm font-medium rounded-md text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500 disabled:bg-gray-400 disabled:cursor-not-allowed"
            disabled={!isFormValid}
          >
            Raise Ticket
          </button>
        </form>
      </div>
    </div>
  );
}
